/**
 * Wysiwyg
 *
 * @author Jérémy Levron <jeremylevron@19h47.fr> (https://19h47.fr)
 */

@layer utilities {
	.wysiwyg {
	}

	.wysiwyg > *:first-child {
		@apply mt-0;
	}

	.wysiwyg > *:last-child {
		@apply mb-0;
	}

	.wysiwyg > p {
		@apply mb-8;
	}

	.wysiwyg > p > a {
		@apply underline;
	}

	.wysiwyg > img {
		@apply w-full h-auto block my-8;
	}
}
